import { useState, useEffect } from 'react'
import { useCurrentAccount, useSignAndExecuteTransaction, useSuiClient } from '@mysten/dapp-kit'
import { Transaction } from '@mysten/sui/transactions'
import { Plus, Trash2, X, ExternalLink, Copy, Check } from 'lucide-react'
import { realWalrusService } from '../lib/realWalrusService'

interface Link {
  label: string  // Changed from 'title' to match Move contract
  url: string
  icon?: string
}

interface Profile {
  id: string
  name: string
  bio: string
  avatar_cid: string
  links: Link[]
  theme: string
}

const PACKAGE_ID = import.meta.env.VITE_LINKTREE_PACKAGE_ID
const REGISTRY_ID = import.meta.env.VITE_REGISTRY_ID

const THEMES = [
  { name: 'Ocean', value: 'ocean', gradient: 'from-blue-500 to-cyan-500' },
  { name: 'Sunset', value: 'sunset', gradient: 'from-orange-500 to-pink-500' },
  { name: 'Forest', value: 'forest', gradient: 'from-green-500 to-emerald-500' },
  { name: 'Purple', value: 'purple', gradient: 'from-purple-500 to-pink-500' },
  { name: 'Dark', value: 'dark', gradient: 'from-gray-800 to-gray-900' },
]

export function ProfilePage() {
  const account = useCurrentAccount()
  const suiClient = useSuiClient()
  const { mutate: signAndExecute } = useSignAndExecuteTransaction()
  
  const [profiles, setProfiles] = useState<Profile[]>([])
  const [loading, setLoading] = useState(true)
  const [showCreateForm, setShowCreateForm] = useState(false)
  const [editingProfile, setEditingProfile] = useState<string | null>(null)
  const [copiedLink, setCopiedLink] = useState<string | null>(null)
  
  // Create form state
  const [newProfile, setNewProfile] = useState({
    name: '',
    bio: '',
    theme: 'ocean',
    avatar: null as File | null,
  })
  
  // Edit link state
  const [newLink, setNewLink] = useState({ label: '', url: '' })
  const [showAddLink, setShowAddLink] = useState(false)

  useEffect(() => {
    if (account) {
      loadUserProfiles()
    }
  }, [account])

  const loadUserProfiles = async () => {
    if (!account) return
    
    try {
      setLoading(true)
      // Get ProfileCap objects owned by user (not the profiles themselves, they are shared objects)
      const ownedCaps = await suiClient.getOwnedObjects({
        owner: account.address,
        filter: {
          StructType: `${PACKAGE_ID}::linktree::ProfileCap`
        },
        options: {
          showContent: true,
          showType: true,
        }
      })

      const profilesData: Profile[] = []
      
      // For each capability, fetch the actual profile
      for (const capObj of ownedCaps.data) {
        if (capObj.data?.content && 'fields' in capObj.data.content) {
          const capFields = capObj.data.content.fields as any
          const profileId = capFields.profile_id
          
          try {
            // Fetch the actual shared profile object
            const profileObj = await suiClient.getObject({
              id: profileId,
              options: {
                showContent: true,
                showType: true,
              }
            })
            
            if (profileObj.data?.content && 'fields' in profileObj.data.content) {
              const fields = profileObj.data.content.fields as any
              console.log('📊 Profile fields:', fields)
              console.log('🔗 Links data:', fields.links)
              console.log('🔍 First link detailed:', JSON.stringify(fields.links[0], null, 2))
              profilesData.push({
                id: profileId,
                name: fields.name,
                bio: fields.bio,
                avatar_cid: fields.avatar_cid,
                links: fields.links || [],
                theme: fields.theme,
              })
            }
          } catch (err) {
            console.error('Error fetching profile:', profileId, err)
          }
        }
      }
      
      setProfiles(profilesData)
    } catch (error) {
      console.error('Error loading profiles:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleCreateProfile = async () => {
    if (!account || !newProfile.name) return

    try {
      setLoading(true)
      
      // Upload avatar to Walrus if provided
      let avatarCid = ''
      if (newProfile.avatar) {
        try {
          const result = await realWalrusService.uploadFile(newProfile.avatar, true)
          avatarCid = result.fileId
        } catch (error) {
          console.error('Avatar upload failed:', error)
          alert('Avatar upload failed, creating profile without avatar')
        }
      }

      const tx = new Transaction()
      tx.setSender(account.address)
      tx.setGasBudget(100000000) // 0.1 SUI
      
      tx.moveCall({
        target: `${PACKAGE_ID}::linktree::create_profile`,
        arguments: [
          tx.object(REGISTRY_ID),
          tx.pure.string(newProfile.name),
          tx.pure.string(avatarCid),
          tx.pure.string(newProfile.bio),
          tx.pure.string(newProfile.theme),
        ],
      })

      signAndExecute(
        { transaction: tx as any },
        {
          onSuccess: () => {
            alert('Profile created successfully! 🎉')
            setShowCreateForm(false)
            setNewProfile({ name: '', bio: '', theme: 'ocean', avatar: null })
            loadUserProfiles()
          },
          onError: (error) => {
            console.error('Error creating profile:', error)
            alert('Failed to create profile. Make sure you have SUI tokens in your wallet. Error: ' + error.message)
          },
        }
      )
    } catch (error) {
      console.error('Error:', error)
      alert('An error occurred')
    } finally {
      setLoading(false)
    }
  }

  const handleAddLink = async (profileId: string) => {
    if (!newLink.label || !newLink.url) return

    try {
      const tx = new Transaction()
      
      tx.moveCall({
        target: `${PACKAGE_ID}::linktree::add_link`,
        arguments: [
          tx.object(profileId),
          tx.pure.string(newLink.label),
          tx.pure.string(newLink.url),
        ],
      })

      signAndExecute(
        { transaction: tx as any },
        {
          onSuccess: () => {
            alert('Link added successfully!')
            setNewLink({ label: '', url: '' })
            setShowAddLink(false)
            loadUserProfiles()
          },
          onError: (error) => {
            console.error('Error adding link:', error)
            alert('Failed to add link')
          },
        }
      )
    } catch (error) {
      console.error('Error:', error)
    }
  }

  const handleRemoveLink = async (profileId: string, index: number) => {
    if (!confirm('Are you sure you want to remove this link?')) return

    try {
      const tx = new Transaction()
      
      tx.moveCall({
        target: `${PACKAGE_ID}::linktree::remove_link`,
        arguments: [
          tx.object(profileId),
          tx.pure.u64(index),
        ],
      })

      signAndExecute(
        { transaction: tx as any },
        {
          onSuccess: () => {
            alert('Link removed successfully!')
            loadUserProfiles()
          },
          onError: (error) => {
            console.error('Error removing link:', error)
            alert('Failed to remove link')
          },
        }
      )
    } catch (error) {
      console.error('Error:', error)
    }
  }

  const copyProfileLink = (profileId: string) => {
    const link = `${window.location.origin}/view/${profileId}`
    navigator.clipboard.writeText(link)
    setCopiedLink(profileId)
    setTimeout(() => setCopiedLink(null), 2000)
  }

  if (!account) {
    return (
      <div className="max-w-4xl mx-auto">
        <div className="bg-gradient-to-br from-gray-800/90 to-gray-900/90 backdrop-blur-lg border border-gray-700/50 rounded-2xl p-8 text-center">
          <h2 className="text-2xl font-bold text-white mb-4">Connect Your Wallet</h2>
          <p className="text-white/70">Please connect your wallet to manage your profiles</p>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-6xl mx-auto space-y-8">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-4xl font-bold text-white mb-2">My Profiles</h1>
          <p className="text-white/60">Manage your LinkTree profiles</p>
        </div>
        <button
          onClick={() => setShowCreateForm(true)}
          className="flex items-center gap-2 px-6 py-3 bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-600 hover:to-purple-600 text-white font-bold rounded-lg transition-all"
        >
          <Plus className="w-5 h-5" />
          Create Profile
        </button>
      </div>

      {/* Create Profile Modal */}
      {showCreateForm && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-gradient-to-br from-gray-800 to-gray-900 border border-gray-700 rounded-2xl p-8 max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-white">Create New Profile</h2>
              <button
                onClick={() => setShowCreateForm(false)}
                className="text-white/60 hover:text-white"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            <div className="space-y-4">
              <div>
                <label className="block text-white/80 mb-2 font-medium">Profile Name *</label>
                <input
                  type="text"
                  value={newProfile.name}
                  onChange={(e) => setNewProfile({ ...newProfile, name: e.target.value })}
                  className="w-full px-4 py-3 bg-gray-900/50 border border-gray-600 rounded-lg text-white placeholder-white/40 focus:outline-none focus:border-blue-500"
                  placeholder="johndoe"
                  maxLength={30}
                />
              </div>

              <div>
                <label className="block text-white/80 mb-2 font-medium">Bio</label>
                <textarea
                  value={newProfile.bio}
                  onChange={(e) => setNewProfile({ ...newProfile, bio: e.target.value })}
                  className="w-full px-4 py-3 bg-gray-900/50 border border-gray-600 rounded-lg text-white placeholder-white/40 focus:outline-none focus:border-blue-500"
                  placeholder="Tell us about yourself..."
                  rows={3}
                  maxLength={200}
                />
              </div>

              <div>
                <label className="block text-white/80 mb-2 font-medium">Avatar Image</label>
                <input
                  type="file"
                  accept="image/*"
                  onChange={(e) => setNewProfile({ ...newProfile, avatar: e.target.files?.[0] || null })}
                  className="w-full px-4 py-3 bg-gray-900/50 border border-gray-600 rounded-lg text-white file:mr-4 file:py-2 file:px-4 file:rounded-lg file:border-0 file:bg-blue-500 file:text-white hover:file:bg-blue-600"
                />
                <p className="text-white/40 text-sm mt-1">Will be stored on Walrus</p>
              </div>

              <div>
                <label className="block text-white/80 mb-2 font-medium">Theme</label>
                <div className="grid grid-cols-5 gap-3">
                  {THEMES.map((theme) => (
                    <button
                      key={theme.value}
                      onClick={() => setNewProfile({ ...newProfile, theme: theme.value })}
                      className={`p-4 rounded-lg border-2 transition-all ${
                        newProfile.theme === theme.value
                          ? 'border-white'
                          : 'border-gray-600 hover:border-gray-500'
                      }`}
                    >
                      <div className={`w-full h-8 rounded bg-gradient-to-r ${theme.gradient} mb-2`} />
                      <p className="text-white/80 text-xs font-medium">{theme.name}</p>
                    </button>
                  ))}
                </div>
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  onClick={handleCreateProfile}
                  disabled={!newProfile.name || loading}
                  className="flex-1 px-6 py-3 bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-600 hover:to-purple-600 disabled:from-gray-600 disabled:to-gray-700 text-white font-bold rounded-lg transition-all"
                >
                  {loading ? 'Creating...' : 'Create Profile'}
                </button>
                <button
                  onClick={() => setShowCreateForm(false)}
                  className="px-6 py-3 bg-gray-700 hover:bg-gray-600 text-white font-medium rounded-lg transition-all"
                >
                  Cancel
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Profiles List */}
      {loading && profiles.length === 0 ? (
        <div className="text-center py-12">
          <div className="animate-spin w-12 h-12 border-4 border-blue-500 border-t-transparent rounded-full mx-auto mb-4"></div>
          <p className="text-white/60">Loading profiles...</p>
        </div>
      ) : profiles.length === 0 ? (
        <div className="bg-gradient-to-br from-gray-800/90 to-gray-900/90 backdrop-blur-lg border border-gray-700/50 rounded-2xl p-12 text-center">
          <div className="text-6xl mb-4">🔗</div>
          <h2 className="text-2xl font-bold text-white mb-2">No Profiles Yet</h2>
          <p className="text-white/60 mb-6">Create your first LinkTree profile to get started!</p>
          <button
            onClick={() => setShowCreateForm(true)}
            className="inline-flex items-center gap-2 px-6 py-3 bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-600 hover:to-purple-600 text-white font-bold rounded-lg transition-all"
          >
            <Plus className="w-5 h-5" />
            Create Your First Profile
          </button>
        </div>
      ) : (
        <div className="grid gap-6">
          {profiles.map((profile) => (
            <div
              key={profile.id}
              className="bg-gradient-to-br from-gray-800/90 to-gray-900/90 backdrop-blur-lg border border-gray-700/50 rounded-2xl p-6"
            >
              <div className="flex items-start justify-between mb-4">
                <div className="flex items-center gap-4">
                  {profile.avatar_cid && (
                    <img
                      src={`https://aggregator.walrus-testnet.walrus.space/v1/${profile.avatar_cid}`}
                      alt={profile.name}
                      className="w-16 h-16 rounded-full object-cover border-2 border-gray-600"
                    />
                  )}
                  <div>
                    <h3 className="text-2xl font-bold text-white">{profile.name}</h3>
                    <p className="text-white/60">{profile.bio}</p>
                    <div className="flex items-center gap-2 mt-2">
                      <span className={`px-3 py-1 rounded-full text-xs font-medium bg-gradient-to-r ${THEMES.find(t => t.value === profile.theme)?.gradient} text-white`}>
                        {THEMES.find(t => t.value === profile.theme)?.name}
                      </span>
                    </div>
                  </div>
                </div>
                <div className="flex items-center gap-2">
                  <button
                    onClick={() => copyProfileLink(profile.id)}
                    className="px-4 py-2 bg-gray-700 hover:bg-gray-600 text-white rounded-lg flex items-center gap-2 transition-all"
                  >
                    {copiedLink === profile.id ? (
                      <>
                        <Check className="w-4 h-4" />
                        Copied!
                      </>
                    ) : (
                      <>
                        <Copy className="w-4 h-4" />
                        Copy Link
                      </>
                    )}
                  </button>
                  <a
                    href={`/view/${profile.id}`}
                    target="_blank"
                    className="px-4 py-2 bg-blue-600 hover:bg-blue-500 text-white rounded-lg flex items-center gap-2 transition-all"
                  >
                    <ExternalLink className="w-4 h-4" />
                    View
                  </a>
                </div>
              </div>

              {/* Links Section */}
              <div className="border-t border-gray-700 pt-4 mt-4">
                <div className="flex items-center justify-between mb-3">
                  <h4 className="text-white font-semibold">Links ({profile.links.length})</h4>
                  <button
                    onClick={() => {
                      setEditingProfile(profile.id)
                      setShowAddLink(true)
                    }}
                    className="px-3 py-1 bg-green-600 hover:bg-green-500 text-white text-sm rounded-lg flex items-center gap-1 transition-all"
                  >
                    <Plus className="w-4 h-4" />
                    Add Link
                  </button>
                </div>

                {profile.links.length === 0 ? (
                  <p className="text-white/40 text-sm italic">No links yet</p>
                ) : (
                  <div className="space-y-2">
                    {profile.links.map((link: any, index) => (
                      <div
                        key={index}
                        className="flex items-center justify-between bg-gray-900/50 p-3 rounded-lg border border-gray-700"
                      >
                        <div className="flex-1">
                          <p className="text-white font-medium">{link.fields?.label || link.label}</p>
                          <a
                            href={link.fields?.url || link.url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="text-blue-400 text-sm hover:underline"
                          >
                            {link.fields?.url || link.url}
                          </a>
                        </div>
                        <button
                          onClick={() => handleRemoveLink(profile.id, index)}
                          className="p-2 text-red-400 hover:text-red-300 hover:bg-red-900/20 rounded transition-all"
                        >
                          <Trash2 className="w-4 h-4" />
                        </button>
                      </div>
                    ))}
                  </div>
                )}

                {/* Add Link Form */}
                {showAddLink && editingProfile === profile.id && (
                  <div className="mt-4 p-4 bg-gray-900/70 border border-gray-600 rounded-lg">
                    <h5 className="text-white font-medium mb-3">Add New Link</h5>
                    <div className="space-y-3">
                      <input
                        type="text"
                        value={newLink.label}
                        onChange={(e) => setNewLink({ ...newLink, label: e.target.value })}
                        placeholder="Link Title (e.g., Twitter)"
                        className="w-full px-4 py-2 bg-gray-800 border border-gray-600 rounded-lg text-white placeholder-white/40 focus:outline-none focus:border-blue-500"
                      />
                      <input
                        type="url"
                        value={newLink.url}
                        onChange={(e) => setNewLink({ ...newLink, url: e.target.value })}
                        placeholder="https://..."
                        className="w-full px-4 py-2 bg-gray-800 border border-gray-600 rounded-lg text-white placeholder-white/40 focus:outline-none focus:border-blue-500"
                      />
                      <div className="flex gap-2">
                        <button
                          onClick={() => handleAddLink(profile.id)}
                          disabled={!newLink.label || !newLink.url}
                          className="px-4 py-2 bg-green-600 hover:bg-green-500 disabled:bg-gray-600 text-white rounded-lg font-medium transition-all"
                        >
                          Add Link
                        </button>
                        <button
                          onClick={() => {
                            setShowAddLink(false)
                            setNewLink({ label: '', url: '' })
                          }}
                          className="px-4 py-2 bg-gray-700 hover:bg-gray-600 text-white rounded-lg transition-all"
                        >
                          Cancel
                        </button>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  )
}
